import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCircle } from '@fortawesome/free-solid-svg-icons';

import ImgWrapper from '../../components/UI/ImgWrapper/ImgWrapper';
import ObservationsBg from '../../assets/images/Observations_bg.png';
import RunRiverBg from '../../assets/images/Run_River_bg.jpg';
import LearningBg from '../../assets/images/Learning_bg.jpg';
import classes from './Home.module.css';

class Home extends Component {
	state = {
		backGround: ""
	};
	
	linkMouseOverHandler = (className) => {
		const viewportWidth = window.innerWidth || document.documentElement.clientWidth;
		if (viewportWidth > 768 && this.state.backGround !== className) {
			this.setState({backGround: className});
		} else if (this.state.background !== "") {
			this.setState({backGround: ""});
		}
	};
	
	linkMouseOutHandler = () => {
		if (this.state.backGround !== "") {
			this.setState({backGround: ""});
		}
	};
	
	render () {
		return (
			<div className={[classes.Home, classes[this.state.backGround]].join(" ")}>
				<Link
					onMouseOut={this.linkMouseOutHandler}
					onMouseOver={() => this.linkMouseOverHandler("Observations")}
					className={classes.HomeLink}
					to="observations_of_a_common_past_adrian_kay_wong">
					<ImgWrapper extraClass="HomeLinkImgWrapper" src={ObservationsBg} />
					<h2 className={classes.HomeLinkHeader}>
						<FontAwesomeIcon className={classes.HomeLinkHeaderCircle} icon={faCircle} />
						Observations of a Common Past: Adrian Kay Wong
					</h2>
					<h3 className={classes.HomeLinkSubheader}>
						Adrian Kay Wong talks about his childhood, inspiration, and his development as an artist in Los Angeles.
					</h3>
				</Link>
				<Link onMouseOut={this.linkMouseOutHandler}
				      onMouseOver={() => this.linkMouseOverHandler("Run-River")}
				      className={classes.HomeLink}
				      to="observations_of_a_common_past_adrian_kay_wong">
					<ImgWrapper extraClass="HomeLinkImgWrapper" src={RunRiverBg} />
					<h2 className={classes.HomeLinkHeader}>
						<span className="line-break"><FontAwesomeIcon className={classes.HomeLinkHeaderCircle} icon={faCircle} />Run River North: </span>
						Adrian Kay Wong
					</h2>
					<h3 className={classes.HomeLinkSubheader}>
						Adrian Kay Wong talks about his childhood, inspiration, and his development as an artist in Los Angeles.
					</h3>
				</Link>
				<Link onMouseOut={this.linkMouseOutHandler}
				      onMouseOver={() => this.linkMouseOverHandler("Learning")}
				      className={classes.HomeLink}
				      to="observations_of_a_common_past_adrian_kay_wong">
					<ImgWrapper extraClass="HomeLinkImgWrapper" src={LearningBg} />
					<h2 className={classes.HomeLinkHeader}>
						<span className="line-break"><FontAwesomeIcon className={classes.HomeLinkHeaderCircle} icon={faCircle} />Learning To Dance: </span>
						Adrian Kay Wong
					</h2>
					<h3 className={classes.HomeLinkSubheader}>
						Adrian Kay Wong talks about his childhood, inspiration, and his development as an artist in Los Angeles.
					</h3>
				</Link>
			</div>
		);
	};
}

export default Home;